<template>
    <div id="new">
        <div class="row">
            <div class="col left fw-bold">Word:</div>
            <div class="col right">
                <Word
                    v-model="data.word"
                    @keydown.enter="loadEdit"
                    @change="loadEdit"
                    :disabled="edit"
                />
            </div>
        </div>
        <div v-if="data.word" class="row">
            <div class="col left"></div>
            <div class="col right">
                <a
                    :href="'https://goo.gl/search/define+' + data.word"
                    target="_blank"
                    class="col-sm-i"
                >
                    define {{ data.word }}
                </a>
            </div>
        </div>

        <template v-if="load">
            <hr />

            <Meaning v-model="data.meanings" @clickChip="clickChip" />

            <hr />

            <div class="row">
                <div class="col left fw-bold">Family:</div>
                <div class="col right">
                    <WordList v-model="data.families" @clickChip="clickChip" />
                </div>
            </div>

            <div class="row">
                <div class="col left fw-bold">Remark:</div>
                <div class="col right">
                    <MultiLine v-model="data.remark" />
                </div>
            </div>

            <hr />

            <div class="row">
                <div class="col">
                    <Button label="Save" @click="save" />
                    <Button
                        label="Clear"
                        class="p-button-secondary"
                        @click="clear"
                    />
                    <Button
                        label="Remove"
                        class="p-button-danger"
                        @click="remove"
                        :disabled="!edit"
                    />
                </div>
            </div>
        </template>
    </div>
    <div id="ifrm"></div>
    <ConfirmDialog />
    <Toast />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { ActiveLoader } from "vue-loading-overlay";

import moment from "moment";

import { initData } from "@/common";
import { getWord, saveWord, removeWord } from "@/firebase";

import Word from "@/components/Word.vue";
import Meaning from "@/components/Meaning.vue";
import WordList from "@/components/WordList.vue";
import MultiLine from "@/components/MultiLine.vue";

var loader: ActiveLoader;

export default defineComponent({
    components: {
        Word,
        Meaning,
        WordList,
        MultiLine,
    },
    methods: {
        save() {
            this.showLoading();

            if (!this.edit) {
                this.data.last_fibonacci = 1;
                this.data.next_review = moment()
                    .add(1, "days")
                    .format("YYYY-MM-DD");
            }

            this.data.save_date = moment().format("YYYY-MM-DD");
            this.data.save_time = moment().format("HH:mm:ss");

            saveWord(this.data)
                .then(() => {
                    this.clear();
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while saving word.");
                })
                .finally(() => {
                    this.hideLoading();
                });
        },
        clear() {
            this.reset();

            this.$router.push("/new");
        },
        remove() {
            this.$confirm.require({
                message: "Are you sure to delete this word?",
                header: "Delete Confirmation",
                icon: "pi pi-info-circle",
                acceptClass: "p-button-danger",
                accept: () => {
                    this.showLoading();

                    removeWord(this.data.word)
                        .then(() => {
                            this.clear();
                        })
                        .catch((err) => {
                            console.log(err);

                            this.errorToast("Error while removing word.");
                        })
                        .finally(() => {
                            this.hideLoading();
                        });
                },
            });
        },
        clickChip(word: string) {
            this.$router.push("/new/" + word);
        },
        loadEdit() {
            if (this.data.word) this.$router.push("/new/" + this.data.word);
        },
        loadWord(word: string) {
            this.showLoading();

            getWord(word)
                .then((doc) => {
                    if (doc.exists) {
                        this.edit = true;
                        this.data = doc.data();
                    } else {
                        this.edit = false;
                        this.data = initData();
                        this.data.word = word;
                    }

                    this.load = true;
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while loading word.");
                })
                .finally(() => {
                    this.hideLoading();
                });
        },
        reset() {
            this.load = false;
            this.edit = false;
            this.data = initData();
        },
        showLoading() {
            loader = this.$loading.show({ color: "#198964" });
        },
        hideLoading() {
            loader.hide();
        },
        errorToast(detail: string) {
            this.$toast.add({
                severity: "error",
                summary: "Error",
                detail: detail,
                life: 3000,
            });
        },
    },
    data() {
        return {
            load: false,
            edit: false,
            data: initData(),
        };
    },
    mounted() {
        let ifrm = document.getElementById("ifrm");

        if (ifrm && !ifrm.offsetLeft) ifrm.style.display = "none";

        let word = this.$route.params.word as string;

        if (word) this.loadWord(word);
    },
    beforeRouteUpdate(to) {
        let word = to.params.word as string;

        if (word) this.loadWord(word);
        else this.reset();
    },
});
</script>

<style scoped lang="less">
#new,
#ifrm {
    display: inline-block;
    vertical-align: top;
}

#new {
    width: 100%;
}

#ifrm {
    width: 30rem;
    height: calc(100vh - 5rem);
}
</style>
