<template>
    <template v-for="(example, index) in model" :key="index">
        <div class="row">
            <div class="col left col-ps-2">
                Example: {{ number + (index + 1) }}
            </div>
            <div class="col right">
                <div class="flex-center">
                    <AddRemove v-model="model" :index="index" :empty="empty" />
                    <MultiLine v-model="example.example" />
                </div>
            </div>
        </div>
    </template>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import { modelWrapper, emptyExample } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    data() {
        return {
            empty: emptyExample(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
</script>
