<template>
    <InputText
        :value="modelValue"
        @input="$emit('update:modelValue', $event.target.value.toLowerCase())"
        @keypress="isAlphabet($event)"
    />
</template>

<script lang="ts">
import { defineComponent } from "vue";

import { isAlphabet } from "@/common";

export default defineComponent({
    props: {
        modelValue: String,
    },
    methods: {
        isAlphabet($event: any) {
            isAlphabet($event);
        },
    },
});
</script>
