<template>
    <template v-for="(definition, index) in model" :key="index">
        <div class="row">
            <div class="col left col-ps-1">
                Definition: {{ number + (index + 1) }}
            </div>
            <div class="col right">
                <div class="flex-center">
                    <AddRemove v-model="model" :index="index" :empty="empty" />
                    <MultiLine v-model="definition.definition" />
                </div>
            </div>
        </div>

        <Example
            v-model="definition.examples"
            :number="number + (index + 1) + '.'"
        />
    </template>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import { modelWrapper, emptyDefinition } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";
import Example from "@/components/Example.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
        Example,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    data() {
        return {
            empty: emptyDefinition(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
</script>
