<template>
    <Button
        icon="pi pi-plus"
        class="p-button-sm"
        @click="add(index)"
        :disabled="modelValue.length >= 5"
    />
    <Button
        icon="pi pi-minus"
        class="p-button-danger p-button-sm"
        @click="remove(index)"
        :disabled="modelValue.length <= 1"
    />
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        modelValue: Array,
        index: Number,
        empty: Object,
    },
    methods: {
        add(index: number) {
            if (this.modelValue) {
                let newValue = [...this.modelValue];
                newValue.splice(
                    index + 1,
                    0,
                    JSON.parse(JSON.stringify(this.empty))
                );
                // spread ... and Object.assign() not work for copying empty

                this.$emit("update:modelValue", newValue);
            }
        },
        remove(index: number) {
            if (this.modelValue && this.modelValue.length > 1) {
                let newValue = [...this.modelValue];
                newValue.splice(index, 1);

                this.$emit("update:modelValue", newValue);
            }
        },
    },
});
</script>
