
import { defineComponent } from "vue";

import { modelWrapper, emptyExample } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    data() {
        return {
            empty: emptyExample(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
