<template>
    <Textarea rows="1" :autoResize="true" v-model="model" />
</template>

<script lang="ts">
import { defineComponent } from "vue";

import { modelWrapper } from "@/common";

export default defineComponent({
    props: {
        modelValue: String,
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
</script>
