<template>
    <template v-for="(meaning, index) in model" :key="index">
        <div class="row">
            <div class="col left fw-bold">Part of Speech: {{ index + 1 }}</div>
            <div class="col right">
                <div class="flex-center">
                    <AddRemove v-model="model" :index="index" :empty="empty" />
                    <PartOfSpeech v-model="meaning.part_of_speech" />
                </div>
            </div>
        </div>

        <Definition v-model="meaning.definitions" :number="index + 1 + '.'" />

        <div class="row">
            <div class="col left col-ps-1">Synonym:</div>
            <div class="col right">
                <WordList v-model="meaning.synonyms" @clickChip="clickChip" />
            </div>
        </div>

        <div class="row">
            <div class="col left col-ps-1">Antonym:</div>
            <div class="col right">
                <WordList v-model="meaning.antonyms" @clickChip="clickChip" />
            </div>
        </div>
    </template>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import { modelWrapper, emptyMeaning } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import PartOfSpeech from "@/components/PartOfSpeech.vue";
import Definition from "@/components/Definition.vue";
import WordList from "@/components/WordList.vue";

export default defineComponent({
    components: {
        AddRemove,
        PartOfSpeech,
        Definition,
        WordList,
    },
    props: {
        modelValue: Array,
    },
    methods: {
        clickChip(word: string) {
            this.$emit("clickChip", word);
        },
    },
    emits: ["update:modelValue", "clickChip"],
    data() {
        return {
            empty: emptyMeaning(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
</script>
